<template>
  <transition name="slider">
      <div class="result-wrap">
          <div class="Widget text-center">
            <div class="image-wrap">
                <div class="compose-image">
                  <img src="" alt="" class="image-result" :src="imgSrc" >
                </div>
                <div class="maimai-icon-wrap">
                  <div class="maimai-icon" ></div>
                  <div class="maimai-icon-desc"></div>
                </div>
            </div>
            <div class="result-btn-wrap text-center">
                <div class="share-btn-tip"></div>
                <div class="js-btn-share" onclick="_czc.push(['_trackEvent', '分享照片', '点击数', '', '', 'js-btn-share']);" @click="showShareBox" v-if="isShare"></div>
                <div class="want-play" v-else @click="infoPlay"></div>
            </div>
          </div>
          <div class="share-box" v-show="shareOnOff" @click=closeShareBox>
            <img src="../common/image/share-tips.png" alt="">
          </div>
      </div>
  </transition>
</template>

<script>
  import {doMain} from  'api/url'
  import axios from 'common/js/http';
  import wx from 'weixin-js-sdk';
    export default {
        name: "",
        data(){
          return{
            isShare:true,
            imgSrc:'',
            shareOnOff:false
          }
        },
        created(){
          this.initWxShare();
          this.initImg()
        },
        methods:{
          successshare(){
            _czc.push(['_trackEvent', '成功分享照片', '点击数', '', '', 'js-btn-share']);
          },
          cancelshare(){
            _czc.push(['_trackEvent', '撤销分享照片', '点击数', '', '', 'js-btn-share']);
          },
          infoPlay(){
            this.$router.push({
              path:'index'
            })
          },
          showShareBox(){
            this.shareOnOff=true;
          },
          closeShareBox(){
            this.shareOnOff=false;
          },
          initWxShare(){
            // console.log(window.location.href.split('#')[0]+'#'+window.location.href.split('#')[1])
            // console.log(encodeURIComponent(window.location.href))
            let url=window.location.href.split('#')[0]+'#'+window.location.href.split('#')[1]+'/';
            console.log(url)
            // axios.get('http://applet.meitu.com/public/index/wx_token',{
            //   params:{
            //     url:url,
            //   }
            // },{withCredentials:false}).then((res)=>{
            //   // console.log(res)
            //   // this.shareContent(res.data)
            // })
            // process.env.API_HOST+'public/index/wx_token'
            this.$jsonp(process.env.API_HOST+'public/index/wx_token',{url:url}).then((res)=>{
                  console.log(res)
                 this.shareContent(res)
            })
          },
          shareContent(data){
            let _this=this;
            wx.config({
              debug: true,
              appId:data.appId,
              timestamp:data.timestamp,
              nonceStr:data.nonceStr ,
              signature:data.signature,
              //需要分享的列表项:发送给朋友，分享到朋友圈，分享到QQ，分享到QQ空间
              jsApiList: [
                'onMenuShareAppMessage','onMenuShareTimeline',
                'onMenuShareQQ','onMenuShareQZone'
              ]
            });
            //处理验证失败的信息
            wx.error(function (res) {
              console.log('验证失败')
            });
            //处理验证成功的信息
            wx.ready(function () {
              //  alert(window.location.href.split('#')[0]);
              //分享给朋友
              wx.onMenuShareAppMessage({
                title: "最美证件照", // 分享标题
                desc: "拍出最美证件照", // 分享描述
                link: '', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                // link: window.location.href.split('#')[0]+'&isShare=yes', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                imgUrl: 'http://wechat.yiwang.com/appwxshare/img/shareImage/thumbnailImage.png', // 分享图标
                success: function (res) {
                  _this.closeBox();
                  _this.successshare();
                },
                cancel: function (res) {
                  _this.closeBox();
                  _this.cancelshare();
                }
              });
              //分享到朋友圈
              // wx.onMenuShareTimeline({
              //   title: "最美证件照", // 分享标题
              //   link:  window.location.href.split('#')[0]+'isShare=yes', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              //   imgUrl: 'http://wechat.yiwang.com/appwxshare/img/shareImage/thumbnailImage.png', // 分享图标
              //   success: function (res) {
              //     _this.closeBox();
              //     _this.successshare();
              //   },
              //   cancel: function (res) {
              //     _this.closeBox();
              //     _this.cancelshare();
              //   }
              // });
              //分享到QQ
              // wx.onMenuShareQQ({
              //   title: "最美证件照", // 分享标题
              //   desc: "拍出最美证件照", // 分享描述
              //   link: window.location.href.split('#')[0]+'isShare=yes', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              //   imgUrl: 'http://wechat.yiwang.com/appwxshare/img/shareImage/thumbnailImage.png', // 分享图标
              //   success: function (res) {
              //     _this.closeBox();
              //     _this.successshare();
              //   },
              //   cancel: function (res) {
              //     _this.closeBox();
              //     _this.cancelshare();
              //   }
              // });
              //分享到QQ空间
              // wx.onMenuShareQZone({
              //   title: "最美证件照", // 分享标题
              //   desc: "拍出最美证件照", // 分享描述
              //   link: window.location.href.split('#')[0]+'isShare=yes', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              //   imgUrl: 'http://wechat.yiwang.com/appwxshare/img/shareImage/thumbnailImage.png', // 分享图标
              //   success: function (res) {
              //     _this.closeBox();
              //     _this.successshare();
              //   },
              //   cancel: function (res) {
              //     _this.closeBox();
              //     _this.cancelshare();
              //   }
              // });
            });
          },
          initImg(){
              if(!this.getUrlKey('imgsrc')){
                    this.$router.push({
                      path:'/index'
                    })
              }else{
                // console.log(this.getUrlKey('imgSrc'))
                this.imgSrc=decodeURIComponent(this.getUrlKey('imgsrc'))
              }
              if(this.getUrlKey('isShare')=='yes'){
                    this.isShare=false;
              }

          },
          getUrlKey:function(name){
            return decodeURIComponent((new RegExp('[?|&]'+name+'='+'([^&;]+?)(&|#|;|$)').exec(location.href)||[,""])[1].replace(/\+/g,'%20'))||null;
          }
        }
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
.share-box{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0,0,0,0.5);
  z-index:999;
  img{
    width: 100%;
  }
}
.result-wrap{
   position: absolute;
   top:0;
   left: 0;
   width: 100%;
   height: 17.8rem;
   background: url("../common/image/bg-make.jpg") no-repeat;
   z-index: 35;
   overflow: hidden
}
.text-center{
  text-align: center;
}
.image-wrap{
  position: relative;
  max-width: 8.4rem;
  margin: 0.72rem auto;
}
.compose-image{
  height: 11.1rem;
  overflow: hidden;
}
.image-result{
  position: relative;
  width: 100%;
  height: auto;
  margin: 0 auto;
  display: block;
  max-height: 13.14rem;
}
.maimai-icon-wrap{
  position: absolute;
  bottom: -0.94rem;
  .maimai-icon{
    width: 2.7rem;
    height: 2.02rem;
    background: url("../common/image/sprite.icons.png");
    background-position: -3.79rem -1.8rem;
    background-size: 941px auto;
  }
  .maimai-icon-desc{
    width:2.666666rem;
    height: 0.533333rem;
    background: url("../common/image/sprite.icons.png");
    background-position: -730px -508px;
    background-size: 941px auto;
    -webkit-animation: iconMove 0.8s linear infinite;
    animation: iconMove 0.8s linear infinite;
  }
}
@-webkit-keyframes iconMove {
  0%, 49.9% {
    -webkit-transform: translateY(0);
  }

  50%, 100% {
    -webkit-transform: translateY(0.106667rem);
  }
}

@keyframes iconMove {
  0%, 49.9% {
    -webkit-transform: translateY(0);
  }

  50%, 100% {
    -webkit-transform: translateY(0.106667rem);
  }
}
.result-btn-wrap{
  .share-btn-tip{
    margin: 0.72rem 0 0.066666rem 5.6933333rem;
    width: 3.36rem;
    height: 1.613333rem;
    background: url("../common/image/sprite.icons.png");
    background-position: -7.33333333rem -3.6rem;
    background-size: 941px auto;
  }
  .js-btn-share{
    display: inline-block;
    width: 5.213333rem;
    height: 1.0666666rem;
    background: url("../common/image/sprite.icons.png");
    background-position: 0 -4.8133333rem;
    background-size: 941px auto;
  }
  .want-play{
    display: inline-block;
    width: 4.133333rem;
    height: 1.133333rem;
    background: url("../common/image/sprite.icons.png");
    background-position: 0 -6.6133333rem;
    background-size: 941px auto;
  }
}
.slider-enter-active, .slider-leave-active{
  transition: all 0.4s
}
.slider-enter, .slider-leave-to{
  transform: translate3d(100%, 0, 0)
}
</style>



